import {useEffect, useState} from 'react'
import './filme.css';
import {useParams, useHistory} from 'react-router-dom';
//useParams √© utilizado para buscar o parametro passado na url, o id passado na url
//useHistory √© utilizado para fazer um controle de navega√ß√£o
import api from '../../services/api';
import {toast} from 'react-toastify'



export default function Filme () {
    const { id } = useParams();
    //instanciamos o useParams dessa forma, desestruturando e pegando apenas o id dos parametros 
    const history = useHistory();
    //instanciamos o useHistory aqui


    const [filme, setFilme] = useState([]); 
    const [loading, setLoading] = useState(true);
    //state de loading serve pra deixar a pagina em carragamento at√© terminar a requisi√ß√£o para api, terminou a requisi√ß√£o, finalizamos o loading --> fazendo isso, podemos utilizar a renderiza√ß√£o condicional, usando if e else no JSX abaixo




    useEffect(() => {
        
        async function loadFilme () {

            const response = await api.get(`r-api/?api=filmes/${id}`);

            if(response.data.length === 0){
                //verifica√ß√£o e diz se o usu√°rio tentou utilizar um id errado nos parametros, logo a api vai retornar uma array vazio, a verifica√ß√£o utiliza o lenght pra ver se h√° algo dentro do array, e se n√£o tiver nada, vamos navegar o usu√°rio para a home
                history.replace('/');
                //navegamos o usuario at√© a home com o history.replace()
                return;
            }

           
            setFilme(response.data);
            setLoading(false);
            //finalizamos o loading atualizando a state loading para false
           
        };

        loadFilme();

        return () => {
        console.log('COMPONENTE DESMONTADO')
        }

    }, [history, id]);

    function salvarFilme () {

        const minhaLista = localStorage.getItem('filmes');

        let filmesSalvos = JSON.parse(minhaLista) || [];
        //hackzinho => caso tenha algo no localstorage, ele vai armazenar dentro da variavel filmesSalvos, caso n√£o tenha "ou ||" ele vai trazer apenas um array vazio para a variavel n√£o ficar vazia

        //Verifica√ß√£o => se tiver um filme salvo com esse mesmo ID, precisamos ignorar
        const hasFilme = filmesSalvos.some((filmeSalvo) =>  filmeSalvo.id === filme.id);
        //.some -> func√ß√£o do javascript que percorre todo o array de filmesSalvos e vai verificar se pelo menos algum item √© igual ao parametro que a gente passar --> se for der tudo certo, o .some devolve um boolean pra gente, true ou false

        if (hasFilme){
        
            toast.info('Voc√™ j√° possui esse filme salvo.')
            return;
            //demos um return aqui pra a execu√ß√£o do c√≥digo parar
        };

        filmesSalvos.push(filme);
        localStorage.setItem('filmes', JSON.stringify(filmesSalvos));
        toast.success('Filme salvo com sucesso')


    };

    
    if(loading){
        return(
            <div className='filme-info'>
                <h1>Carregando seu filme...</h1>
            </div>
        )
    }else{

        return (
            <div className='filme-info'>
        
                    <h1>{filme.nome}</h1>
                    <img src={filme.foto} alt={filme.nome} />
                    <h3>Sinopse</h3>
                    {filme.sinopse}

                    <div>


                        <button onClick={salvarFilme}>Salvar</button>


                        <a target='blank' href={`https://www.youtube.com/results?search_query=${filme.nome} Trailer`}>Trailer</a>
                       
                    </div>
                
            </div>
        )

    };
    
};